.section-detail-prd
    .detail-product
        @apply flex -lg:flex-col -lg:gap-base
        .detail-prd-banner-main
            @apply flex -lg:flex-col w-full lg:w-[calc(820/1400*100%)] lg:rem:pr-[76px]
            .banner-prd-thumb
                @apply w-full lg:w-[calc(96/744*100%)] lg:pr-4 -lg:order-2 -lg:mt-5
                .swiper-detail-prd-thumbs
                    .swiper-wrapper
                        .swiper-slide
                            &.swiper-slide-thumb-active
                                .img 
                                    @apply border-primary-1
            .banner-prd-main 
                @apply w-full lg:w-[calc(650/744*100%)] 
        .detail-prd-main-content
            @apply w-full lg:w-[calc(580/1400*100%)]