.section-news-detail-header
    .news-detail-header
        @apply grid grid-cols-1 xl:grid-cols-2 -xl:container -xl:gap-base
        .block-content
            @apply -xl:order-2
            @apply xl:rem:pl-[260px] xl:pr-base xl:pr-19 xl:rem:py-[112px]
            .date-social
                .social-list
                    @apply flex-y-center gap-x-3
                    .social-item
                        &.item-face
                            a
                                @apply text-[rgba(24,119,242,1)]