.section-solution
    .solution
        .solution-header
            @apply w-full lg:w-[calc(1200/1400*100%)]
        .block-solution-list
            .swiper-pagination
                @apply static flex mt-base
                span
                    @apply m-0 rounded-none h-0.75 bg-utility-gray-200 flex-1 
                    &.swiper-pagination-bullet-active
                        @apply bg-secondary-primary-3-50 bg-opacity-50