.section-contact
    .contact 
        .contact-main
            @apply flex -lg:flex-col -lg:gap-base
            .contact-business-info
                @apply w-full lg:w-[calc(560/1400*100%)] pr-base lg:pr-20 -lg:order-2
                .contact-business-info-child
                    .business-info-list
                        .business-info-item 
                            &::after
                                @apply content-[''] block w-full border-t border-utility-gray-200 my-5
            .block-contact-form 
                @apply w-full lg:w-[calc(840/1400*100%)] -lg:order-1
                .block-form
                    .form-main
                        @apply mt-5
                        .form-wrap
                            @apply flex flex-col gap-y-5
                            .block-inp
                                input
                                    @apply w-full rem:h-[46px] py-3 px-5 rounded-full border border-utility-gray-200 outline-none placeholder:text-body-3 placeholder:text-utility-gray-500 text-body-1
                                textarea
                                    @apply w-full rem:h-[227px] py-3 px-5 border border-utility-gray-200 outline-none rounded-2