.section-banner-main
    .swiper-banner
        .swiper-wrapper 
            .swiper-slide
                &::before
                    @apply content-[''] absolute bottom-0 left-0 z-2 h-[50%] md:h-full block w-full md:w-[55%] xl:w-[calc(800/1920*100%)] rounded-r-full pointer-events-none
                    @apply bg-white bg-opacity-30 backdrop-blur-[30px] border-r border-b border-t border-white 
                &::after
                    @apply content-[''] absolute bottom-0 right-0 z-1 h-[50%] md:h-full block w-[55%] xl:w-[calc(1120/1920*100%)] pointer-events-none 
                    @apply rounded-l-full border-b border-l border-t border-white -md:hidden
                .block-content-banner
                    @apply flex justify-start
                    .content-banner
                        @apply translate-x-[-160%] 
                &.swiper-slide-active
                    &::after
                        @screen md
                            animation: animateBoxRight 0.8s linear 0.5s forwards
                    .block-content-banner
                        .content-banner
                            animation: animateBoxContent 0.8s linear 0.5s forwards
                &.even-slide
                    &::after
                        @apply md:rotate-180 right-[unset] left-0
                    &::before
                        @apply rotate-180 left-[unset] right-0
                    .block-content-banner
                        @apply flex justify-end 
                        .content-banner
                            @apply text-right
                    &.swiper-slide-active
                        &::after
                            @screen md
                                animation: animateEvenBoxRight 0.8s linear 0.5s forwards
                        .block-content-banner
                            .content-banner
                                animation: animateEvenBoxContent 0.8s linear 0.5s forwards

@keyframes animateBoxRight
    from
        transform: translateX(0)
    to          
        transform: translateX(-71.3%)
@keyframes animateBoxContent
    from
        transform: translateX(-160%)
    to          
        transform: translateX(0)
// animation even
@keyframes animateEvenBoxRight
    from
        transform: translateX(0) rotate(180deg)
    to          
        transform: translateX(71.3%) rotate(180deg)

@keyframes animateEvenBoxContent
    from
        transform: translateX(160%) 
    to          
        transform: translateX(0) 


