.section-tech-specs
    .tech-specs
        .tech-specs-list
            @apply -mx-5
            .tech-specs-item
                @apply px-5 
                @screen lg
                    &:not(:nth-child(3)):not(:nth-child(6))
                        @apply border-r border-utility-gray-200
                    &:nth-child(-n+3)
                        .child
                            @apply border-b border-utility-gray-200
                @screen sm
                    &:not(even)
                        @apply border-r border-utility-gray-200
                    &:nth-child(-n+4)
                        .child
                            @apply border-b border-utility-gray-200
                .child
                    @apply border-b border-utility-gray-200