.section-training-course
    .training-course
        @apply flex -lg:flex-col -lg:gap-base
        .block-content
            @apply w-full lg:w-[calc(660/1400*100%)] pr-15
        .block-slide
            @apply w-full lg:w-[calc(740/1400*100%)]
            .embla-slide-2
                @apply rotate-180
                .embla__slide .img
                    @apply -rotate-180
            .embla-slide-1,.embla-slide-2
                @apply  overflow-hidden
                .embla__viewport 
                    @apply overflow-hidden
                    .embla__container
                        @apply h-[500px] md:rem:h-[880px] flex flex-col
                        .embla__slide
                            @apply flex-[0_0_auto] min-w-0
                            &:not(:last-child)
                                @apply mb-5