.section-core-values
    .core-values
        .core-values-list
            .core-values-item 
                @screen -md
                    &:first-child,&:last-child
                        @apply row-start-2 row-end-4
                    &:last-child
                        @apply col-start-3 col-end-5
                    &:first-child
                        @apply col-start-1 col-end-3
                    &:nth-child(2)
                        @apply col-start-2 col-end-4
                        