.section-home-overview
    .home-overview
        @apply flex items-center -lg:flex-col -lg:gap-base
        .content-main
            @apply w-full lg:w-[calc(640/1400*100%)] lg:pr-15
        .block-img 
            @apply w-full lg:w-[calc(760/1400*100%)]
            .thumb
                @apply img-ratio ratio:pt-[720_760]