.btn
	@apply cursor-pointer select-none transition-300
	&.loading
		@apply bg-transparent #{!important}
		span,.icon
			@apply opacity-0
		&::before
			content: ''
			animation-duration: 0.5s
			@apply w-5 h-5 border-3 border-white/40 border-t-white rounded-full inline-block
			@apply absolute animate-spin
	&.btn-primary
		@apply inline-flex items-center justify-center py-3 px-6 rounded-full border border-primary-3 text-primary-3 text-body-2 transition-300
		&:hover
			@apply text-white border-primary-1 bg-primary-1
		&.active
			@apply text-white border-primary-1 bg-primary-1
	&.btn-white
		@apply inline-flex items-center justify-center py-3 px-6 rounded-full border border-white text-body-2 transition-300 text-white
		&:hover
			@apply bg-primary-1 border-primary-1
	&.btn-seemore
		@apply inline-flex items-center gap-x-2 text-body-3 text-white 
		i
			@apply rem:text-[15px] transition-300 
		&:hover
			i
				@apply translate-x-1
	&.btn-slide
		@apply w-10 h-10 rounded-1 flex-center border border-utility-gray-950 text-base text-utility-gray-950 transition-300
		&:hover
			@apply bg-primary-1 border-white text-white
	&.btn-slide-circ
		@apply flex-center w-10 lg:w-16 h-10 lg:h-16 rounded-full border border-[rgba(109,110,113,1)] text-2xl text-[rgba(109,110,113,1)]
		&:hover
			@apply bg-primary-1 border-white text-white
	&.btn-black
		@apply inline-flex items-center justify-center py-3 px-6 rounded-full border border-white bg-black text-body-2 transition-300 text-white
		&:hover
			@apply bg-primary-1 border-transparent