.footer
    .footer-top 
        .footer-child
            .footer-logo
                @apply w-full -xl:flex-x-center xl:w-[calc(303/1400*100%)] -xl:pb-6 -xl:mb-6 -xl:border-b -xl:border-white -xl:border-opacity-30
                a img
                    @apply max-h-10 md:max-h-[48px] rem:lg:max-h-[68px] w-auto
            .footer-block-content
                @apply w-full xl:w-[calc(990/1400*100%)]
    .footer-bottom
        .footer-child
            .dadangky-img
                img
                    @apply max-h-10 w-auto
            .social-list
                @apply flex items-center -md:justify-end gap-x-2 rem:lg:ml-[304px]
                .social-item a
                    @apply flex-center w-10 h-10 rounded-full bg-primary-1 text-lg text-white transition-300
                    &:hover
                        @apply bg-white text-primary-1
.hover-link
    @apply relative
    &::before
        @apply content-[''] absolute border-0 left-0 block w-0 border-b border-transparent transition-300
    &:hover::before
         @apply border-white border-opacity-40 w-full