.section-service
    .service
        .service-header
            @apply w-full lg:w-[calc(824/1400*100%)]
        .service-list
            .service-item
                &:nth-child(3)
                    @apply -lg:col-span-2 
                &:not(:last-child)
                    @apply bg-white rounded-2 p-6 flex flex-col -md:col-span-full
                    .icon
                        @apply w-20 h-20
                        img
                            @apply max-w-full object-contain
                    .service-info
                        @apply mt-10 flex-1 flex flex-col items-start
                        .info-name
                            @apply heading-2-regular line-clamp-2
                        .info-desc
                            @apply mt-5 flex-1 flex items-end line-clamp-2
                        .btn-detail
                            @apply mt-5
                &:last-child
                    @apply relative col-span-full overflow-hidden rounded-4
                    .block-thumb
                        .thumb
                            @apply img-ratio ratio:pt-[4_6] md:ratio:pt-[650_1400]
                    .service-info
                        @apply absolute-x-center bottom-0 pb-6 px-4 lg:pb-15 text-center 
                        @apply w-full lg:w-[calc(960/1400*100%)]
                        .info-name
                            @apply heading-2-regular text-white line-clamp-2
                        .info-desc
                            @apply mt-5 text-body-1 text-white line-clamp-2
                        .btn-detail
                            @apply mt-5 
