.news-list-grid
    @apply grid grid-cols-1 -lg:gap-base lg:grid-cols-[minmax(0,calc(920/1400*100%)),minmax(0,calc(480/1400*100%))]
    .block-news-first
        @apply lg:pr-base
    .side-news 
        @apply grid -md:grid-cols-1 md:grid-cols-2 lg:grid-cols-[auto] lg:grid-rows-2 gap-base
    .news-item
        .block-thumb
            @apply lg:h-full
            .thumb
                @apply lg:p-0 lg:h-full
.events-header-tab-list 
    @apply flex items-center text-body-1 text-utility-gray-400 overflow-x-auto
    .events-header-tab-item 
        &:not(:last-child)
            &::after
                @apply content-['/'] text-sm inline-block mx-3
        &.active
            @apply text-primary-3