.section-solution-detail
    .solution-detail
        @apply -xl:container
        .solution-detail-content-list 
            @apply  flex flex-col -xl:gap-base
            .solution-detail-content-item
                @screen xl
                    &:nth-child(odd)
                        .block-content
                            @apply pl-20 rem:pr-[260px]
                    &:nth-child(even)
                        .block-media 
                            @apply order-2
                        .block-content
                            @apply order-1 pr-20 rem:pl-[260px]