@layer components
    form.submitting
        button
            @apply pointer-events-none relative
            &::before
                content: ''
                animation-duration: 0.5s
                @apply w-5 h-5 border-3 border-primary-1/40 border-t-primary-1 rounded-full inline-block
                @apply absolute animate-spin left-full ml-3
    .grid-cols-5-res
        @apply grid grid-cols-2 md:grid-cols-3 lg:grid-cols-4 xl:grid-cols-5
    .grid-cols-4-res
        @apply grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4
    .grid-cols-3-res
        @apply grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3
    // product item
    .product-item
        @apply relative overflow-hidden rounded-4
        &:hover
            @screen xl
                &::before
                    @apply opacity-0 invisible
                &::after
                    @apply opacity-100 visible h-[42%]
                .product-info 
                    .info-child
                        @apply translate-y-0
                        .product-seemore
                            @apply opacity-100 visible
        &::before
            @apply content-[''] absolute bottom-0 left-0 z-1 w-full h-[45%] bg-[linear-gradient(to_bottom,rgba(0,0,0,0),rgba(0,0,0,0.8))] transition-300 -xl:opacity-0 -xl:invisible
        &::after
            @apply content-[''] absolute bottom-0 left-0 z-1 w-full h-[44%] xl:h-[38%] xl:bg-[#B0001F] xl:bg-opacity-90 xl:opacity-0 xl:invisible transition-300
            @apply -xl:bg-[linear-gradient(to_bottom,rgba(176,0,31,0),rgba(176,0,31,1))]
        .product-thumb
            a
                @apply img-ratio ratio:pt-[440_320] 
                // img
        .product-info 
            @apply absolute bottom-0 left-0 z-2 w-full py-5 md:py-10 px-4 md:px-6
            .info-child 
                @apply flex flex-col items-start gap-y-3 md:gap-y-5 justify-center transition-300
                @apply rem:xl:translate-y-[calc(100%-78px)]
                .info-name
                    @apply xl:flex xl:items-end heading-4 text-white border-primary-2 xl:h-[calc(1em*1.4*2)]
                    a
                        @apply line-clamp-2 
                &::after
                    @apply content-[''] w-full border-t-2 border-primary-2 order-2
                .product-seemore
                    @apply order-3 xl:opacity-0 xl:invisible 
    // product home 
    .product-home-item 
        @apply relative overflow-hidden rounded-4
        &:hover
            .product-info
                @apply xl:translate-y-0
        .product-thumb 
            a
                @apply img-ratio ratio:pt-[560_440] 
        .product-info 
            @apply absolute bottom-0 left-0 xl:rem:translate-y-[calc(100%+8px)] w-full bg-white bg-opacity-10 backdrop-blur-[20px] transition-300
            @apply py-6 px-10
            .info-name
                @apply uppercase heading-2-regular text-white line-clamp-4
            .info-desc
                @apply mt-5 text-body-1 text-white line-clamp-4
            .product-seemore
                @apply mt-5
        // 
    .solution-item 
        @apply relative overflow-hidden rounded-4 rem:pt-[56px] h-full transition-300
        &:hover
            @screen xl
                @apply rem:-translate-y-[56px]
                .solution-info 
                    @apply translate-y-0
                    .btn-discover
                        @apply opacity-100 visible
        .solution-thumb 
            a
                @apply img-ratio ratio:pt-[424_320] rounded-4
        .solution-info 
            @apply absolute bottom-0 left-0 flex flex-col items-center gap-y-2 w-full p-5 lg:p-8 bg-[linear-gradient(to_bottom,rgba(0,0,0,0),rgba(0,0,0,1))] transition-300 will-change-transform
            @apply xl:rem:translate-y-[calc(100%-(65px+(32px*2)))]
            .info-name 
                @apply heading-2-regular xl:flex xl:items-end text-center text-white line-clamp-2 uppercase
                @apply xl:h-[calc(1em*1.35*2)]
                a
                    @apply line-clamp-2
            .btn-discover
                @apply flex-center flex-col gap-y-2 w-25 lg:w-30 h-25 lg:h-30 rounded-full bg-primary-1 text-white border border-transparent xl:opacity-0 xl:invisible transition-300 
                &:hover
                    @apply bg-transparent border border-white
                .icon
                    @apply text-xl
                .content
                    @apply text-body-4 lg:text-body-1 uppercase
    .other-services-item
        &:hover .block-info .info-name
            @apply text-primary-1
        .block-info
            @apply py-6
            .info-name
                @apply heading-2-regular uppercase transition-300
            .type-service
                @apply inline-block mt-5 py-2 px-5 rounded-1 text-body-3 text-white bg-primary-1 pointer-events-none
    .news-item 
        @apply relative overflow-hidden rounded-4
        .block-thumb 
            .thumb 
                @apply img-ratio ratio:pt-[270_480] rounded-4
        .news-info 
            @apply flex-between-center gap-x-5 p-5 bg-[linear-gradient(to_bottom,rgba(0,0,0,0),rgba(0,0,0,1))]
            @apply absolute bottom-0 left-0 w-full
            .info-name 
                @apply heading-4 text-white line-clamp-2 
                
            .btn-seemore
                @apply whitespace-nowrap
    .news-item-big 
        @apply relative overflow-hidden rounded-4
        .block-thumb 
            .thumb 
                @apply img-ratio ratio:pt-[580_880] rounded-4
        .news-info 
            @apply p-5 sm:p-10 bg-[linear-gradient(to_bottom,rgba(0,0,0,0),rgba(0,0,0,1))]
            @apply absolute bottom-0 left-0 w-full
            .info-name 
                @apply heading-4 text-white line-clamp-2 
            .info-desc 
                @apply mt-5 text-body-1 text-white line-clamp-2 sm:line-clamp-4
            .btn-seemore
                @apply mt-5
    .learners-can-item
        @apply flex flex-col overflow-hidden rounded-4
        &:hover .learners-can-info
            @apply bg-primary-1
            
        .block-thumb 
            .thumb
                @apply img-ratio ratio:pt-[200_320]
        .learners-can-info
            @apply flex-1 p-6 bg-utility-gray-500 transition-300
            .info-content 
                @apply text-body-1 text-white line-clamp-5
    .tool-list 
        @apply fixed right-10 bottom-6.5 z-10
        .tool-item 
            .button-to-top
                @apply  flex-center w-10 h-10 rounded-full bg-primary-3 border border-white text-base text-white transition-300
                &:hover
                    @apply bg-primary-1 border-primary-1
    #modal
        @apply fixed z-999 inset-0 bg-black bg-opacity-30 translate-x-full transition-300
        @apply delay-[0.3s]
    .menu-mobile
        @apply fixed top-0 left-0 z-[1000] w-[70%] sm:w-[55%] h-full bg-white -translate-x-full 
        @apply transition-300
        .block-logo
            @apply flex-center py-[20px] border-b border-utility-gray-400 bg-utility-gray-950
            a img
                @apply h-7 w-auto
        .menu-list
            @apply flex flex-col gap-y-[12px] pt-5 px-4
            &>li
                @apply flex flex-col gap-y-[8px]
                .title 
                    @apply flex-between-center 
                    &>a
                        @apply block transition-300
                    &.active
                        &>a
                            @apply text-primary-1
                        &::after
                            @apply text-primary-1 rotate-90
                &:not(.no-next)
                    .title
                        &::after
                            @apply content-['\f105'] font-awesome text-lg transition-300
                &.no-next
                    .title
                        &>a
                            @apply w-full
                &>ul
                    @apply flex flex-col p-[8px] pl-[12px] bg-utility-gray-50 hidden
                    &>li
                        &>a
                            @apply block py-1
                &:hover
                    &>.title
                        &>a
                            @apply text-primary-1
                &.active
                    @apply text-primary-1
                
    .pointer-animate
        @apply absolute bottom-4.5 right-[22.5%] z-2 pointer-events-none 
        animation: animatePointer 1.8s linear infinite
        img
            @apply rem:max-w-[48px] h-auto 
        @keyframes animatePointer 
            0%
                transform: translateX(0)
            50%
                transform: translateX(r(12px))
            100%
                transform: translateX(0)
    #popupForm
        // w-[calc(744/1920*100%)]
        @apply fixed top-1/2 left-0 -translate-x-full -translate-y-1/2 z-[1000] w-full rem:max-w-[784px] px-container transition-300 will-change-transform
        .wrap-popupForm
            @apply relative pt-16 pb-7 px-7 bg-white rounded-tr-5
            .btn-close
                @apply flex-center w-6 h-6 rounded-full bg-primary-1 absolute right-0 top-0 text-white rem:text-[14px]
            .popupForm-form
                .main-inp
                    @apply flex flex-col gap-y-4 mt-8
                    .block-inp
                        @apply w-full
                        .inp,.textarea
                            @apply placeholder:text-body-3 placeholder:text-primary-3/50
                            @apply w-full h-12 rounded-2 py-2 px-6 bg-utility-gray-50 outline-none border-none
                        .textarea
                            @apply rem:h-[170px] 
    //active full page
    body
        &.isOpenModal
            #modal
                @apply translate-x-0 delay-[0s]
        &.isOpenMenu
            @screen -lg
                .menu-mobile
                    @apply translate-x-0 delay-[0.3s]
        &.isOpentPopupForm
            #popupForm
                @apply -translate-x-1/2 left-1/2 delay-[0.3s]
    .pagination
        @apply flex items-center gap-x-5 mt-base
        .pagination-item 
            a   
                @apply flex-center w-10 h-10 rounded-1 bg-white text-body-1 text-black transition-300
                &:hover
                    @apply bg-primary-1 text-white
            &.active a
                @apply bg-primary-1 text-white

              


