header
    @apply fixed top-0 left-0 w-screen bg-utility-gray-950 
    @apply z-10 
    .header-child
        @apply -lg:py-3
        .header-logo
            a img
                @apply h-7 md:h-10 w-auto
        .header-language
            @apply relative text-body-3 uppercase 
            &:hover
                .header-language-list
                    @apply opacity-100 visible translate-y-0
            .header-language-active
                @apply flex items-center text-primary-2
                ul
                    li
                        a
                            @apply flex items-center gap-x-3
                            &::after
                                @apply content-['\f078'] font-awesome text-xl xl:text-2xl text-primary-1
                li:not(.wpml-ls-current-language)
                    @apply hidden
            .header-language-list
                @apply absolute rem:top-[calc(100%+4px)] left-0 w-full bg-white rounded-1 shadow-shadow-1 translate-y-3 opacity-0 invisible transition-300
                &::before
                    @apply content-[''] absolute bottom-full left-0 w-full h-1
                ul
                    li
                        a
                            @apply inline-block w-full p-1
                li.wpml-ls-current-language
                    @apply hidden  
        .header-menu
            @apply -lg:hidden
            &>ul
                @apply flex
                &>li
                    
                    &>a
                        @apply inline-block rem:py-[26px] px-7 text-body-2 text-white transition-300
                        &:hover 
                            @apply text-primary-1 bg-primary-2
                    &.active 
                        &>a
                            @apply text-primary-1 bg-primary-2
                    &:hover
                        .dropdown-product-list
                            @apply opacity-100 visible
                    .dropdown-product-list
                        @apply absolute top-full left-0 flex w-full bg-white opacity-0 invisible transition-300
                        @apply flex 
                        .tabslet-tab
                            @apply flex flex-col w-full rem:max-w-[340px]
                            @apply gradient-3
                            &>li
                                @apply flex-1
                                &>a
                                    @apply flex-y-center w-full h-full heading-4 text-white pl-15 py-10
                                &.active
                                    &>a
                                        @apply bg-primary-1
                        .tabslet-content
                            @apply flex-1 p-17
                            @apply grid grid-cols-4 grid-rows-6 gap-6 transition-300
                            &>li
                                &.active
                                    &>a
                                        @apply text-primary-1
                                &>a
                                    @apply block w-full transition-300
                                    &:hover
                                        @apply text-primary-1