.section-general-introduction
    .general-introduction
        .general-introduction-header
            @apply w-full lg:w-[calc(1296/1400*100%)] lg:mx-auto
        .general-introduction-gallery
            .gallery-img
                @screen -md
                    &:nth-child(2)
                        @apply col-span-full
                    &:first-child,&:last-child
                        @apply row-start-2 row-end-3
                        @apply ratio:pt-[3_5]
                    &:first-child
                        @apply col-start-1 col-end-2
                    &:last-child
                        @apply col-start-2 col-end-3
                    